html{
  background-color: rgb(22, 21, 21);
  color: rgb(215, 210, 210);
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  height: 100%;
  overflow-y: hidden;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.height-100-percent{height: 100%;}

.App {display: flex;flex-direction: column;height: 100%;}

.ws-fb-10{flex-basis: 10% !important;}
.ws-fb-100{flex-basis: 100% !important;}
.ws-fb-90{flex-basis: 90% !important;}
.ws-fb-20{flex-basis: 20% !important;}
.ws-fb-80{flex-basis: 80% !important;}
.ws-fb-15{flex-basis: 15% !important;}
.ws-fb-70{flex-basis: 70% !important;}
.ws-fb-50{flex-basis: 50% !important;}
.ws-fb-40{flex-basis: 40% !important;}
.ws-fb-60{flex-basis: 60% !important;}

.align-items-center{align-items: center;}
.justify-contents-center{justify-content: center;}

.ws-d-flex{display: flex !important;}
.ws-flex-col{flex-direction: column;}
.ws-flex-row{flex-direction: row;}
.ws-d-none{
    display: none !important;
}

.ws-fs-14{font-size: 14px !important;}
.ws-fs-18{font-size: 18px !important;}
.ws-fs-22{font-size: 22px !important;}

.btn{
    display: inline-block;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;
    width: 100px;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
    background-color: transparent;
    border: 1px solid transparent;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    border-radius: 0.25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
.btn-primary{
    color: rgb(215, 210, 210);
    background-color: #0d6efd;
    border-color: #0d6efd;
}
.btn-primary:hover{
    color: rgb(215, 210, 210);
    background-color: #0b5ed7;
    border-color: #0a58ca;
}
.btn-tertiary{
    color: rgb(215, 210, 210);
    background-color: #6c757d;
    border-color: #6c757d;
}
.btn-tertiary:hover{
    color: rgb(215, 210, 210);
    background-color: #5c636a;
    border-color: #565e64;
}

.ws-m-r-20{margin-right: 20px !important;}
.ws-p-lr-10{margin: 0 10px 0 10px !important;}
.ListView {display: flex;flex-direction: column;flex-basis: 100%;align-items: center;margin-bottom: 10px;height: 100%;}
.ListView .list-todos{display: flex;flex-direction: column;flex-basis: 90%;overflow-y: scroll;border-radius: 10px;width: 70%;padding: 10px;}
.ListView .no-todos-present{display: flex;align-items: center;justify-content: center;font-size: 22px !important;}
.Card {display: flex;background-color: rgb(39, 36, 36); margin-top: 10px;box-sizing: border-box;}
.TodoContainer {flex-direction: row; min-height: 60px; background-color: #161515 !important; border-radius: 10px;align-items: center; padding: 10px; margin-left: 10px; margin-right: 10px;}
.TodoContainer .todo-title{display: flex;flex-basis: 30%;}
.TodoContainer .todo-title .title{display: flex;flex-basis: 50%;}
.TodoContainer .todo-complete-by{display: flex;flex-basis: 30%;}
.TodoContainer .todo-complete-by .complete-by{display: flex;flex-basis: 50%;}
.TodoContainer .actions{display: flex;flex-direction: row-reverse;flex-basis: 40%;}
.TodoContainer .actions .delete-todo{display: flex;flex-basis: 5%;}
.TodoContainer .actions .edit-todo{display: flex;flex-basis: 5%;}
.Filter {display: none;flex-direction: row;justify-content: center; align-items: center;position: absolute;top: 38px;left: 42%;}
.filter-card{display: flex;flex-direction: column;height: 300px; width: 310px !important; border-radius: 10px;padding: 10px;border: 1px solid white; background-color: #161515 !important}
.label-container{display: flex;flex-direction: column;justify-content: center;margin: 10px;}
.label-container .label{line-height: 34px !important;display: flex;}
.label-container .date-range{display: flex;flex-direction: row;height: 34px !important;}
.label-container .date-range .from-date, .label-container .date-range .to-date{border-radius: 10px !important;}
.label-container .priority{display: flex;flex-direction: row;align-items: center;height: 34px !important;}
.label-container .priority .priority_1{margin-right: 5px;}
.label-container .priority .priority_2{margin-right: 5px;}
.label-container .priority .priority_3{margin-right: 5px;}
.label-container .priority .label_1{margin-right: 10px;}
.label-container .priority .label_2{margin-right: 10px;}
.label-container .priority .label_3{margin-right: 10px;}
.footer-actions{display: flex;flex-direction: row-reverse; align-items: flex-end;}
.Sort {display: none;flex-direction: row;justify-content: center; align-items: center;position: absolute;top: 38px;left: 42%;}
.sort-card{display: flex;flex-direction: column;height: 300px; width: 310px !important; border-radius: 10px;padding: 10px;border: 1px solid white; background-color: #161515 !important;}
.sort-container{display: flex;flex-direction: column;align-items: center;margin: 10px;}
.label-container{display: flex;flex-direction: column;justify-content: center;margin: 10px;}
.label-container .label{line-height: 34px !important;display: flex;}
.label-container .sort-radio{display: flex;flex-direction: row;height: 34px !important;align-items: center;}
.label-container .sort-radio .sort{margin-right: 10px;}
.label-container .sort-radio .label_1{margin-right: 10px;}
.label-container .sort-radio .label_2{margin-right: 10px;}
.footer-actions{display: flex;flex-direction: row-reverse; align-items: flex-end;}
.QuickLaunch {display: flex;flex-direction: row;align-items: center;justify-content: center;}
.quick-launch-card{display: flex;flex-direction: row;flex-basis: 50%;border-radius: 50px;align-items: center; justify-content: center;height: 38px;}
.list-view{display: flex;font-size: 22px !important;cursor: pointer;width: 5%;align-items: center;justify-content: center;height: 38px;}
.week-view{display: flex;font-size: 22px !important;cursor: pointer;width: 5%;align-items: center;justify-content: center;height: 38px;}
.filter{display: flex;font-size: 18px !important;cursor: pointer;width: 5%;align-items: center;justify-content: center;height: 38px;}
.sort{display: flex;font-size: 22px !important;cursor: pointer;width: 5%;align-items: center;justify-content: center;height: 38px;}
.add{display: flex;font-size: 22px !important;cursor: pointer;width: 5%;align-items: center;justify-content: center;height: 38px;}
.WeekCalendar {display: flex;flex-direction: column;align-items: center;flex-basis: 10%;height:10%;justify-content: center;width: 70%;}
.WeekCalendar .week-view{display: flex;flex-direction: row;align-items: center;flex-basis: 100%;justify-content: center;border-radius: 50px; width: 100%;}

.left-container{display: flex;flex-direction: row;align-items: center; justify-content: center;flex-basis: 5%;height: 38px !important;}

.center-container{display: flex;flex-direction: row;align-items: center; justify-content: center;flex-basis: 90%;height: 38px !important;}
.center-container .week-day{display: flex;flex-direction: column;align-items: center; justify-content: center;flex-basis: calc(100%/7);}
.center-container .week-day .day{display: flex;flex-direction: row;align-items: center; justify-content: center;flex-basis: 50%;}
.center-container .week-day .date{display: flex;flex-direction: row;align-items: center; justify-content: center;flex-basis: 50%;}

.right-container{display: flex;flex-direction: row;align-items: center; justify-content: center;flex-basis: 5%;height: 38px !important;}
.WeekView {display: flex;flex-direction: column;flex-basis: 100%;align-items: center;margin-bottom: 10px;height: 90%;}
.WeekView .list-todos{display: flex;flex-direction: column;flex-basis: 90%;overflow-y: scroll;border-radius: 10px;width: 70%;padding: 10px;}
.WeekView .no-todos-present{display: flex;align-items: center;justify-content: center;font-size: 22px !important;}
